.App {
  text-align: center;
  overflow: hidden;
}.App-header {
  background-color: #282c34;
  height: 200px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  font-size: 36px;
  color: white;
}.footer{
  background-color: #282c34;
  height: 50px;
  width: 100%;
  bottom: 0;
  position: fixed;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: white;
}.button{
  background-color: #282c34;
  border-radius: 5px;
  color: white;
}.button:hover{
  background-color: white;
  color: #282c34;
  cursor: pointer;
}

.App-logo {
  margin-top:10px;
  height: 40px;
  pointer-events: none;
}@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite alternate .8s linear;
  }
}@keyframes App-logo-spin {
  from {
    transform: rotate(40deg);
  }
  to {
    transform: rotate(-40deg);
  }
}

.tuote-section{
  width:60%;
  height: 687px;
  float:left;
  background-color: rgb(220, 255, 255);
  overflow: auto;
}.tuotteet{
  margin-top: 10px;
}.tuote{
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  margin-left: .75%;
  margin-top: 1%;
  width:24%;
  float:left;
  padding-bottom:20px;
  padding-top:20px;
}.tuote-img{
  height:150px;
  width:150px;
  object-fit: cover;
}.pink{
  color:rgb(255, 47, 127);
}

.ostoskori-section{
  width:40%;
  height: 687px;
  float:left;
  background-color: rgb(255, 255, 255);
  overflow: auto;
}.ostoskori-amount{
  margin-left: 400px;
  padding-bottom: 10px;
}.ostoskori-empty{
  padding: 20px;
  color:#282c34; 
  font-weight: bold;
}.ostoskori-ostokset{
  background-color: rgb(225, 255, 255);
  border-radius: 20px;
  height: 500px;
  overflow: auto;
}.ostoskori-button{
  position: fixed;
  bottom: 88px;
  right: 25%;
  font-size: 20px;
  font-weight: bold;
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  color:rgb(255, 47, 127);
}.ostoskori-button:hover{
  background-color: rgb(255, 47, 127);
  color:white;
}.ostoskori-hinta{
  position: fixed;
  bottom: 80px;
  right: 2%;
  font-size: 40px;
  font-weight: bold;
  padding: 10px;
  color:rgb(255, 47, 127);
}.ostos{
  height: 100px;
}.ostos-p{
  padding-top: 10px;
  float: left;
  padding-left: 50px;
}.ostos-img{
  height: 80px;
  float: right;
  margin-top:10px;
  margin-right: 40px;
  border-radius: 10px;
}

.tilaus{
  display: none;
  z-index: 2;
  height: 80%;
  width: 60%;
  position: fixed;
  top: 10%;
  left: 20%;
  background-color: #282c34;
  border-radius: 20px;
  color: white;
  border: 5px solid white;
}.tilaus-input{
  font-size:20px;
  border-radius: 5px;
}.tilaus-button{
  transform: scale(2);
}.tilaus-finish{
  display:none;
}